export default class Utils {


    static buildSunBurstSequences(glycanList,features){
        let map = {};
        for (let glycan of glycanList) {
            let sequence = "";
            for (let feature of features) {
                if(feature == "galactose"){
                    sequence = sequence + this._checkGalactose(glycan);
                } else if (feature == "fucose"){
                    sequence = sequence +this._checkFucose(glycan);
                } else if (feature == "bisected"){
                    sequence = sequence + this._checkBisected(glycan);
                } else if (feature == "mannose"){
                    sequence = sequence + this._checkMannose(glycan);
                } else if (feature == "antenna"){
                    sequence = sequence + this._checkAntennas(glycan);
                } else if (feature == "sialic_acid"){
                    sequence = sequence + this._checkSialicAcid(glycan);
                }
            }
            let key = sequence.substr(0,sequence.length-1);

            if(key in map){
                map[key] = map[key] + glycan.percentage;
            } else {
                map[key] = glycan.percentage;
            }
        }
        let sequences =[];
        for(let key in map){
            let ar = [];
            ar.push(key);
            ar.push(map[key]);
            sequences.push(ar);
        }
        return sequences;
    }

    static _checkFucose(glycan){
        if(glycan.oxfordFeature.fucose){
            return "Fucose-";
        }
        return "NotFucose-";
    }


    static _checkBisected(glycan){
        if(glycan.oxfordFeature.bisected ){
             return "Bisected-";
        }
        return "NotBisected-";
    }

    static _checkMannose(glycan){
        if(glycan.oxfordFeature.mannose !== 0 ){
            return "HighMannose-";
        }
        return "Not_HighMannose-";
    }

    static _checkAntennas(glycan){
        return "Antenna_"+glycan.oxfordFeature.antennas+"-";
    }

    static _checkGalactose(glycan){
        return "Galactose_"+glycan.oxfordFeature.galactose+"-";
    }

    static _checkSialicAcid(glycan){
        return "Sialic_"+glycan.oxfordFeature.SialicAcid+"-";
    }
}


