export default class NetworkBuilder {

    static buildJson(glycanlist){
        let json = {};
        json.nodes = glycanlist;
        json.links = [];
        for (let i = 0; i < glycanlist.length; i++) {
            const size = map[key][0].composition.calculateSize();
            for(let j = 0; j < glycanlist.length; j++){
                if(size + 1 == map[innerKey][0].composition.calculateSize() && this._levenshteinDistance(map[key][0].composition.toString(),map[innerKey][0].composition.toString()) == 1) {
                    let link = {
                        source: i,
                        target: j
                    };
                    json.links.push(link);
                }
            }

        }
        return json;
    }


    static buildJsonComposition(glycanlist){
        let json = {};
        json.links = [];
        var map = {};
        for (let glycan of glycanlist) {
            let key = glycan.composition.toString();
            if( key in map) {
                map[key].push(glycan);
            } else {
                map[key] = [glycan];
            }
        }
        json.nodes = Object.keys(map).map(function (key) { return map[key]; });
        let i = 0;
        for (let key in map) {
            const size = map[key][0].composition.calculateSize();
            let j = 0;
            for(let innerKey in map){
                if(size + 1 == map[innerKey][0].composition.calculateSize() && this._levenshteinDistance(map[key][0].composition.toString(),map[innerKey][0].composition.toString()) == 1){

                    let link = {
                        source: i,
                        target: j,
                        difference: this._differenceMonosaccharide(map[innerKey][0].composition,map[key][0].composition)

                    };
                    json.links.push(link);
                }
                j++;
            }
            i++;
        }
        return json;
    }


    static _levenshteinDistance(a, b) {
        if(a.length === 0) return b.length;
        if(b.length === 0) return a.length;

        // swap to save some memory O(min(a,b)) instead of O(a)
        if(a.length > b.length) {
            var tmp = a;
            a = b;
            b = tmp;
        }

        var row = [];
        // init the row
        for(let index = 0; index <= a.length; index++){
            row[index] = index;
        }

        // fill in the rest
        for(let i = 1; i <= b.length; i++){
            var prev = i;
            for(var j = 1; j <= a.length; j++){
                var val;
                if(b.charAt(i-1) == a.charAt(j-1)){
                    val = row[j-1]; // match
                } else {
                    val = Math.min(row[j-1] + 1, // substitution
                        prev + 1,     // insertion
                        row[j] + 1);  // deletion
                }
                row[j - 1] = prev;
                prev = val;
            }
            row[a.length] = prev;
        }

        return row[a.length];
    }


    static _differenceMonosaccharide(a,b){

        if(a.Fucose != b.Fucose){
            return 'Fuc';
        }
        if(a.Hex != b.Hex){
            return 'Hex';
        }
        if(a.HexNAc != b.HexNAc){
            return 'HexNAc';
        }
        if(a.NeuAc != b.NeuAc){
            return 'NeuAc';
        }
        if(a.NeuGc != b.NeuGc){
            return 'NeuGc';
        }
        return "";
    }
}


