export default class OxfordFeatures{
    constructor(oxfordId){
        if(!oxfordId || oxfordId.length === 0){
            throw new Error("Cannot parse OxfordId");
        } else {
            this._oxfordId = oxfordId;
            this._parseOxfordId(oxfordId);
        }

    }

    _parseOxfordId(oxfordId){

        this._bisected = oxfordId.indexOf("B") != -1;
        this._fucose = oxfordId.indexOf("F") != -1;

        if(oxfordId.indexOf("S")!= -1){
            this._sialicAcid = oxfordId.charAt(oxfordId.indexOf("S")+1);
        } else {
            this._sialicAcid = 0;
        }
        if(oxfordId.indexOf("A")!= -1){
            this._antennas = oxfordId.charAt(oxfordId.indexOf("A")+1);
        } else {
            this._antennas = 0;
        }

        if(oxfordId.indexOf("G") != -1){
            this._galactose = oxfordId.charAt(oxfordId.indexOf("G")+1);
        } else {
            this._galactose = 0;
        }
        if(oxfordId.indexOf("M") != -1){
            this._mannose = oxfordId.charAt(oxfordId.indexOf("M")+1);
        } else {
            this._mannose = 0;
        }
    }


    get oxfordId(){
        return this._oxfordId;
    }

    get sialicAcid(){
        return this._sialicAcid;
    }
    
    get antennas(){
        return this._antennas;
    }

    get bisected(){
        return this._bisected;
    }

    get fucose(){
        return this._fucose;
    }

    get galactose(){
        return this._galactose;
    }

    get mannose(){
        return this._mannose;
    }
}


