import Composition from './Composition';
import GlycoLogue from './GlycoLogue';
import OxfordFeatures from './OxfordFeatures';

export default class Glycan {

    constructor(options) {



        this._id = options.id;
        this._glytoucanId = options.glytoucanId;
        this._compLebrilla = options.lebrillacomp;
        this._composition = new Composition(options.composition);
        //this._glycologueId = new GlycoLogue(options.glycologue);
        //this._oxfordFeatures = new OxfordFeatures(options.oxford);
        this._percentage = options.percentage;
    }

    size(){
        this._composition.size();
    }

    get id (){
        return this._id;
    }

    get oxfordFeature(){
        return this._oxfordFeatures;
    }

    get percentage(){
        return this._percentage;
    }

    get composition(){
        return this._composition;
    }

}



