export default class GlycoLogue {

    constructor(glycologueId){
        if(!glycologueId || glycologueId.length != 9){
            throw new Error("GlycoLogueId cannot be null or undefined");
        } else {
            this._glycologueId = glycologueId;
        }
    }

    get GlycoLogueId(){
        return this._glycologueId;
    }

    numberOfMannose(){
        return this._glycologueId[0];
    }

    numberOfFucose(){
        return this._glycologueId[1];
    }

    isBisectedBoolean(){
        return Boolean(this._glycologueId[2]);
    }

    isBisectedNumber(){
        return this._glycologueId[2];
    }

    extensionFirstBranch(){
        return this._glycologueId[3];
    }

    extensionSecondBranch(){
        return this._glycologueId[4];
    }

    extensionThirdBranch(){
        return this._glycologueId[5];
    }

    extensionFourthBranch(){
        return this._glycologueId[6];
    }

    numberOfGalactose(){
        return this._glycologueId[7];
    }

    numberOfSialicAcid(){
        return this._glycologueId[8];
    }
}


